* {
  box-sizing: border-box;
}
:root {
  --margin-color: rgb(232, 101, 101);
}

button {
  margin: 10px;
  font-size: 17px;
  border-radius: 20px;
  cursor: pointer;
  border: 1px solid black;
  outline: none;
  padding: 5px 15px;
}
button:hover {
  background-color: gray;
}
input {
  margin: 10px;
  font-size: 17px;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}
select {
  outline: none;
}
.hidden {
  display: none;
}

.fontChooser {
  padding: 10px;
  background-color: #f0ede9;
  margin-right: 0px;
  display: inline-block;
}
.colorChooser {
  display: inline-block;
  padding: 10px;
  background-color: #f0ede9;
}

body {
  display: flex;
  flex-direction: column;
}
/* Text Editor */
.textEditor {
  /* margin: auto; */
  margin-top: 20px;
  background-color: rgb(250, 243, 244);
  font-family: "Custom Font";
  height: 598px;
  width: 440px;
  border: 1px solid blue;
  overflow-x: hidden;
  outline: none;
  padding: 15px;
  overflow-y: scroll;
}

.middler {
  max-width: 500px;
  text-align: center;
  margin: 30px 0px;
  padding: 10px;
  background-color: #f0ede9;
}
.pageContainer {
  height: 568px;
  margin: auto;
  width: 400px;
  display: flex;
  background-color: rgb(240, 237, 233);
  flex-direction: column;
  border: 1px solid black;
  margin-bottom: 40px;
  position: relative;
  opacity: 1;
}

.navBar {
  display: block;
}
