.upper {
  flex: 1;
  min-height: 10%;
  display: flex;
}

.lower {
  min-height: 90%;
  max-height: 568px;
  flex: 9;
  display: flex;
}
.pageContainer div {
  font-size: 10pt;
  overflow: hidden;
  outline: none;
  line-height: 1.5em;
}
.pageContainer .shadow {
  top: 0px;
  left: 0px;
  height: 100%;
  z-index: -2;
  width: 100%;
  background-image: linear-gradient(grey, white);

  position: absolute;
}

.pageTopLeft {
  padding-left: 6px;
  width: 100%;
  flex: 1.3;
  max-width: calc(130% / 10.3);
  border-bottom: 2px solid var(--margin-color);
  border-right: 2px solid var(--margin-color);
}
.pageTop {
  width: 100%;
  padding-right: 6px;
  flex: 9;
  max-width: calc(900% / 10.3);
  z-index: 3;
  border-bottom: 2px solid var(--margin-color);
}

.pageLeft {
  padding-left: 6px;
  flex: 1.3;
  max-width: calc(130% / 10.3);
  border-right: 2px solid var(--margin-color);
}
.pageWrapper {
  height: 100%;
  flex: 9;
}

.page {
  /* font-variant: normal; */
  font-family: "Custom Font";
  display: inline-block;
  height: 100%;
  padding-right: 6px;
  padding-left: 6px;
  width: 100%;
  /* overflow: scroll; */
  word-break: break-all;
}

