/* Image List */
.imageList {
  position: relative;
  overflow-x: scroll;
  display: flex;
  flex-direction: row;
  width: 100%;
  background-color: rgb(250, 243, 244);
  margin: auto;
  padding: 10px;
  border: 1px solid blue;
  min-height: 650px;
}
.imageList .imageContainer {
  background-color: white;
  border: 1px solid black;
  margin-right: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 10px;
  position: relative;
  flex-direction: column;
}
.imageContainer img {
  height: 568px;
  width: 400px;
}
.imageContainer canvas {
  height: 568px;
  width: 400px;
}
.imageContainer .downloadBtn {
  min-height: 30px;
  min-width: 150px;
  text-decoration: none;
  color: black;
  background-color: rgb(202, 201, 201);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  margin: 10px;
  border: 1px solid black;
}
.downloadBtn:hover {
  background-color: gray;
}
.imageContainer .deleteImgBtn {
  border-radius: 50%;
  background-color: rgb(202, 201, 201);
  outline: none;
  padding: 0px;
  width: 20px;
  font-size: 10px;
  line-height: 14px;
  border: none;
  height: 20px;
  font-weight: bolder;
  color: white;
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
}
.deleteImgBtn:hover {
  background-color: grey;
}
.generatePdfBtn {
  width: 300px;
  margin-bottom: 10px;
}

.deleteAllImgBtn {
  width: 300px;
  height: 30px;
}
.imageList .message {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  font-size: large;
  left: 50%;
}
/* Image List */

.buttonHolder {
  padding: 20px;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}
